var React = require('react');
var ReactDOM = require('react-dom');
import AppComponent from './components/app.jsx';
import openSocket from 'socket.io-client'
const cSocket = openSocket()
global.cSocket = cSocket
Notification.requestPermission()
cSocket.on('inbox', (datax) => {
	const messageBefore = localStorage.getItem('message_inbox') ? JSON.parse(localStorage.getItem('message_inbox')) : []
	messageBefore.push(datax)
	localStorage.setItem('message_inbox', JSON.stringify(messageBefore))

	new Notification(datax.from, {
		body : datax.message,
		icon : 'https://help.webex.com/community/image/2032/2.png?a=4261'
	})
})
ReactDOM.render((
	<AppComponent/>
), document.getElementById('siether'));


