import React from 'react';
import { Nav, NavItem, NavLink } from 'reactstrap';

export default class Inbox extends React.Component {
	constructor() {
		super()
		this.state = {
			message : []
		}
	}
	_saveLocalStorage(data) {
		localStorage.setItem('message_inbox', data)
	}

	componentDidMount() {
		if (localStorage.getItem('message_inbox')) {
			this.setState({
				message : JSON.parse(localStorage.getItem('message_inbox'))
			})
		}

		cSocket.on('connected', (data) => {
			console.log(data)
		})
		cSocket.on('inbox', (data) => {
			if (data) {
				this.setState({
					message : this.state.message.concat([{date : data.date, message : data.message, from : data.from}])
				})
				this._saveLocalStorage(JSON.stringify(this.state.message))
			}
		})
	}

	_renderMessage() {
		if (this.state.message.length <= 0) {
			return (
				<div>
					<center>
						<b>No Log Record</b>
					</center>
				</div>
			)
		} else {
			return this.state.message.map((log, index) => {
				return (
					<div key={index} tabIndex={index}>
						<span>
							<b>{log.from}</b>
							<p>
								<b>{log.date}</b> - {log.message}
							</p>
						</span>
					</div>
				)
			})
		}
	}

	render() {
		return (
			<div>
				<div className="panel panel-primary">
					<div className="panel-heading">
						<h4>INBOX</h4>
						<span>A Magic Realtime Jabber Application</span>
					</div>
					<div className="panel-body">
						{this._renderMessage()}
					</div>
				</div>
			</div>
		);
	}
}


