import React, {Component} from 'react'
import {
	Button,
	Container,
	Row,
	Col,
	Card,
	CardTitle,
	CardSubtitle,
	CardBody
} from 'reactstrap'

import Navbar from './navbar.jsx'
import Inbox from './inbox.jsx'
import Outbox from './outbox.jsx'
import AddOutbox from './add_outbox.jsx'

export default class Home extends Component {
	constructor(props) {
		super(props)
		this.state = {
			dataLog : [],
			scroll : true,
			route : 'home',
			btnScroll : 'Stop Scroll'
		}
	}

	componentDidMount() {
		cSocket.on('log', (data) => {
			if (data) {
				this.setState({
					dataLog : this.state.dataLog.concat([{date : data.date, message : data.message}])
				})
				this._scrollBottom()
			}
		})
	}

	componentDidUpdate() {
		this._scrollBottom()
	}

	_scrollBottom() {
		if (this.state.scroll) {
			this.el.scrollTop = this.el.scrollHeight
		}
	}

	_renderLog() {
		if (this.state.dataLog.length <= 0) {
			return (
				<div>
					<center>
						<b>No Log Record</b>
					</center>
				</div>
			)
		} else {
			return this.state.dataLog.map((log, index) => {
				return (
					<div key={index} tabIndex={index}>
						<span>
							<b>{log.date}</b> - {log.message}
						</span>
					</div>
				)
			})
		}
	}
	_render() {
		// INBOX
		if (this.state.route == 'message') {
			return (
				<div className="row">
					<div className="col-md-6">
						<Inbox/>
					</div>
					<div className="col-md-6">
						<Outbox/>
					</div>
				</div>
			)
		} if (this.state.route == 'outbox') {
			return (
				<Outbox/>
			)
		} if (this.state.route == 'add_outbox') {
			return (
				<AddOutbox/>
			)
		} else {
			if (this.state.dataLog.length <= 0) {
				return (
					<div>
						<center>
							<b>No Log Record</b>
						</center>
					</div>
				)
			} else {
				return this.state.dataLog.map((log, index) => {
					return (
						<div key={index} tabIndex={index}>
							<span>
								<b>{log.date}</b> - {log.message}
							</span>
						</div>
					)
				})
			}
		}
	}

	_stopScroll() {
		if (this.state.scroll) {
			this.setState({
				scroll : false,
				btnScroll : 'Enable Smart Scroll'
			})
		} else {
			this.setState({
				scroll : true,
				btnScroll : 'Stop Scroll'
			})
		}
	}
	_renderFooter() {
		if (this.state.route == 'message') {
			return (
				<button className="btn btn-default" onClick={() => this.setState({route : 'add_outbox'})} ref={'buttonOutBox'}>
					Add Message
				</button>
			)
		} else if (this.state.route == 'home') {
			return (
				<button className="btn btn-default" onClick={() => this._stopScroll()} ref={'buttonStop'}>
					{this.state.btnScroll}
				</button>
			)
		}
	}
	_changeRoute(route) {
		console.log(route)
		this.setState({
			route : route
		})
	}
	render() {
		return (
			<Container>
				<Row>
					<nav className="navbar navbar-default">
						<div className="container-fluid">
							<div className="navbar-header">
								<a className="navbar-brand" href="javascript:void(0)" onClick={() => { this._changeRoute('home')}}>AKSARAPAY</a>
							</div>
							<ul className="nav navbar-nav">
								<li><a href="javascript:void(0)" onClick={() => { this._changeRoute('message')}}>Message</a></li>
							</ul>
						</div>
					</nav>
				</Row>
				<Row style={{marginTop: 10}}>
					<Col md="1"></Col>
					<Col md="10">
						<div className="panel panel-default">
							<div
								className="panel-body"
								style={{overflowY : 'auto', maxHeight : '400px'}}
								id="panel-log"
								ref={el => {this.el = el}}>
								{this._render()}
							</div>
							
							<div className="panel-footer">
								{this._renderFooter()}
								<center>
									<small>
										<a href="javascript:void(0)" title="Hisa Log">AKSARAPAY</a> By Creativefull 2016
									</small>
								</center>
							</div>
						</div>
					</Col>
				</Row>
			</Container>
		)
	}
}


