import React from 'react';
import Inbox from './inbox.jsx'
import Outbox from './outbox.jsx'

export default class AddOutBox extends React.Component {
	constructor() {
		super()
		this.state = {
			message : '',
			id_jabber : '',
			outbox : false
		}
	}
	_saveLocalStorage(data) {
		try {
			const messageBefore = localStorage.getItem('message_outbox') ? JSON.parse(localStorage.getItem('message_outbox')) : []
			messageBefore.push(data)
			localStorage.setItem('message_outbox', JSON.stringify(messageBefore))

			// SEND MESSAGE
			cSocket.emit('send_message', data)
		} catch (e) {
			console.error("ERROR", e)
		}
	}

	_submit() {
		this._saveLocalStorage({
			date : new Date(),
			message : this.state.message,
			id_jabber : this.state.id_jabber
		})

		this.setState({
			outbox : true
		})
	}

	_renderForm() {
		if (this.state.outbox) {
			return (
				<div className="row">
					<div className="col-md-6">
						<Inbox/>
					</div>
					<div className="col-md-6">
						<Outbox/>
					</div>
				</div>
			)
		} else {
			return (
				<div>
					<div className="form-group">
						<label className="label-control">ID Jabber</label>
						<input value={this.state.id_jabber} className="form-control" onChange={(text) => this.setState({id_jabber : text.target.value})}></input>
					</div>
					<div className="form-group">
						<label className="label-control">Message</label>
						<textarea className="form-control" onChange={(text) => this.setState({message : text.target.value})} value={this.state.message}></textarea>
					</div>
					<div className="form-group">
						<button className="btn btn-primary" onClick={() => this._submit()}>SEND</button>
					</div>
				</div>
			)
		}
	}

	render() {
		return (
			<div>
				{this._renderForm()}
			</div>
		);
	}
}


