import React from 'react';

export default class Outbox extends React.Component {
	constructor() {
		super()
		this.state = {
			message : []
		}
	}
	_saveLocalStorage(data) {
		localStorage.setItem('message_outbox', data)
	}

	componentDidMount() {
		if (localStorage.getItem('message_outbox')) {
			this.setState({
				message : JSON.parse(localStorage.getItem('message_outbox'))
			})
		}
	}

	_renderMessage() {
		if (this.state.message.length <= 0) {
			return (
				<div>
					<center>
						<b>Nothing</b>
					</center>
				</div>
			)
		} else {
			return this.state.message.map((log, index) => {
				return (
					<div key={index} tabIndex={index}>
						<span>
							<b>{log.id_jabber}</b>
							<p>
								<b>{log.date}</b> - {log.message}
							</p>
						</span>
					</div>
				)
			})
		}
	}

	render() {
		return (
			<div>
				<div className="panel panel-danger">
					<div className="panel-heading">
						<h4>OUTBOX</h4>
						<span>A Magic Realtime Jabber Application</span>
					</div>
					<div className="panel-body">
						{this._renderMessage()}
					</div>
				</div>
			</div>
		);
	}
}


